* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
  background-image: url("./back.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(243, 243, 243, 0.2);
  color:#f3f3f3
}

.App {
  display: flex;
  align-items: center;
  flex-direction: column;
}

section {
  margin: 2rem 0 1rem 0;
}

.controls {
  display: flex;
  align-items: center;
  flex-direction: column;
}

input {
  text-align: center;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border-radius: 10px;
}
.tittle{
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
margin: auto;
}

button {
  width: 80%;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  background-color: #16b831ab;
  color: #f3f3f3;
  border: none;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
}

button:hover {
  background-color: #19d113b0;
  cursor: pointer;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  text-align: center;
  margin-bottom: 2rem;
}

.nutrients ul {
  display: flex;
  width: 35rem;
  color:rgb(236, 234, 234);
  justify-content: space-evenly;
  font-size: 20px;
}

.meals {
  display: flex;
}

img {
  width: 100%;
  margin-bottom: 1rem;
}

article {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 0 1rem;
  max-width: 300px;
  /* box-shadow: 0 4px 8px 2px rgba(26, 22, 22, 0.15); */
  background-color:darkorchid ; 
  border-radius: 15px;

}

ul {
  list-style: none;
}

.instructions {
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

a {
  border-radius: 15px;
  text-decoration: none;
  background-color: #2ae704ab;
  color: #f3f3f3;
  width: fit-content;
  padding: 0.5rem 1rem;
}

a:hover {
  background-color: #6c13d1;
  cursor: pointer;
}

@media only screen and (max-width: 1024px) {
  .meals {
    flex-direction: column;
    align-items: center;
  }

  .nutrients ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
}